/************************************************
   GENERAL                              @general

   The base styles for the page, text
   and links. Includes utility class styles
************************************************/
:root {
  --primary: #232F3E;
  --secondary: #F3A847;
  --tertiary: #B12704;
  --light: #fafafa;
  --dark: #272f32;
  --error: rgb(228, 46, 1);
  --spacing-one: calc(1rem * 0.5);
  --spacing-two: calc(1rem);
  --font-primary-style: "Quicksand", Helvetica, Arial, sans-serif;
  --font-secondary-style: "Vidaloka", "Times New Roman", Times, serif;
}
* {
  box-sizing: border-box;
}
html,
body {
  min-height: 100%;
}
body {
  margin: 0;
  padding: 0;
  font-family: var(--font-primary-style);
  font-weight: 400;
  line-height: 1.5;
  font-size: 16px;
  color: var(--dark);
  background-color: #fff1e9;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: var(--font-secondary-style);
  font-weight: 500;
  margin: 5px 0;
  line-height: 1.25;
}
h1 {
  font-size: 2.25rem;
}
h2 {
  font-size: 2rem;
}
h3 {
  font-size: 1.75rem;
}
h4 {
  font-size: 1.5rem;
}
h5 {
  font-size: 1.25rem;
}
h6 {
  font-size: 1rem;
}
p {
  font-size: 1.1rem;
  margin-top: 0;
}
a {
  color: #694873;
  text-decoration: none;
  font-weight: bold;
}
a:hover {
  text-decoration: underline;
}
header {
  background: #694873;
}
header nav ul {
  display: flex;
  align-items: center;
  padding: 0;
  margin: 0;
}
header nav a {
  color: var(--light);
}
header h1 {
  line-height: 1.5em;
}
button {
  font-size: 1rem;
  text-decoration: none;
  padding: 0.25rem 1rem;
  border-radius: .5rem;
  border: none;
  outline: none;
  cursor: pointer;
  margin: 0 1px;
  background-color: #C0E5C8;
}
button:hover {
  opacity: .8;
}
button[disabled] {
  cursor: default;
  opacity: .8;
}
img {
  max-width: 100%;
}
span[role="img"] {
  cursor: default;
}
form {
  width: 40%;
}
input {
  font-size: 1rem;
  padding: 0.25rem;
  border: 1px solid var(--dark);
  outline: none;
  border-radius: 3px;
  transition: box-shadow 0.2s;
}
input:focus {
  border: 1px solid var(--secondary);
  box-shadow: 0 0 .25rem rgba(0, 0, 0, .5);
}
ul li {
  list-style-type: none;
}
.nav-link {
  display: flex;
}
.container {
  width: 85%;
  max-width: 1200px;
  margin-left: auto;
  margin-right: auto;
}
.flex-row {
  display: flex;
  flex-wrap: wrap;
}
.space-between {
  justify-content: space-between;
}
.flex-end {
  justify-content: flex-end;
}
.mx-1 {
  margin-right: var(--spacing-one);
  margin-left: var(--spacing-one);
}
.mx-2 {
  margin-right: var(--spacing-two);
  margin-left: var(--spacing-two);
}
.my-1 {
  margin-top: var(--spacing-one);
  margin-bottom: var(--spacing-one);
}
.my-2 {
  margin-top: var(--spacing-two);
  margin-bottom: var(--spacing-two);
}
.px-1 {
  padding-right: var(--spacing-one);
  padding-left: var(--spacing-one);
}
.px-2 {
  padding-right: var(--spacing-two);
  padding-left: var(--spacing-two);
}
.py-1 {
  padding-top: var(--spacing-one);
  padding-bottom: var(--spacing-one);
}
.py-2 {
  padding-top: var(--spacing-two);
  padding-bottom: var(--spacing-two);
}
.card {
  width: 25%;
  text-align: center;
}
.error-text {
  color: var(--error);
}
.header span[role="img"] img {
  width: 1.5em;
  margin-right: 0.5em;
}
.cart-closed {
  background-color: #8B728E !important;    
  font-size: 1.5rem !important;  
  text-align: center !important;  
  border: 3px solid #FFF !important;
  top: 7px !important;
}
.cart-closed span svg path {
  fill: #FFF;
}
.cart-closed:hover {
  cursor: pointer;
}
.categories {
  margin-top: -5px;
  padding: 1em;
  background-color: #16302B;
  color: #FFF;
}
@media screen and (max-width: 768px) {
  .card {
    width: 100%;
  }
}
